
/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-groundhogg-forms {
	.gh-form-shortcode-placeholder {
		background-color: #f0f0f0;
		border: 2px dashed #ccc;
		border-radius: 4px;
		padding: 2em;
		text-align: center;
		color: #666;

		p:first-child {
			font-weight: 600;
			margin-bottom: 0.5em;
		}

		p:last-child {
			margin-bottom: 0;
			font-size: 0.9em;
		}
	}

	.gh-form-shortcode-loading {
		padding: 2em;
		text-align: center;
		color: #666;
		font-style: italic;
	}

	.gh-form-shortcode-preview {
		//border: 1px solid #e0e0e0;
		//border-radius: 4px;
		padding: 1em;
		//background-color: #fff;
	}
}
